.container {
  max-width: 1320px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
  outline: 1px solid brown;
}

.header {
  padding-top: 23px;
  padding-bottom: 29px;
}
.header__top {
  display: flex;
  align-items: center;
  color: var(--white);
}
.header__catalog-btn {
  margin-right: 26px;
}
.header__search-form {
  margin-right: 26px;
}
.header__actions {
  display: flex;
  gap: 14px;
}

.catalog-menu__btn-main {
  position: relative;
}
.catalog-menu__ground {
  position: absolute;
  width: 53.33vw;
  padding: 41px 10px 28px 37px;
  top: 112px;
  left: 16px;
  border-radius: 14px;
  height: 403px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.25);
  background: var(--white);
  opacity: 0;
  visibility: hidden;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__list {
  width: 48%;
  height: 343px;
  padding-right: 23px;
  overflow-x: hidden;
  overflow-y: auto;
}
.catalog-menu__list::-webkit-scrollbar {
  width: 4px;
}
.catalog-menu__list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu__list::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.catalog-menu .catalog-menu__ground.catalog-menu__ground--open {
  opacity: 1;
  visibility: visible;
}
.catalog-menu__item {
  position: relative;
}
.catalog-menu__item:not(:last-child) {
  margin-bottom: 26px;
}
.catalog-menu__svg {
  fill: currentColor;
}
.catalog-menu__btn-secondary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  width: 100%;
  color: var(--cod-gray);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.catalog-menu__btn-secondary--active {
  color: var(--red);
}

.dropdown {
  position: fixed;
  width: 22.7%;
  height: 343px;
  top: 147px;
  left: 31%;
  background: var(--white);
  opacity: 0;
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.dropdown::-webkit-scrollbar {
  width: 4px;
}
.dropdown::-webkit-scrollbar-thumb {
  background-color: #FF9E9E;
  border-radius: 9px;
}
.dropdown::-webkit-scrollbar-track {
  background-color: #e0e0e0;
  border-radius: 9px;
}
.dropdown--active {
  opacity: 1;
  visibility: visible;
}
.dropdown__list {
  padding: 6px;
  width: 100%;
  height: 100%;
}
.dropdown__item {
  margin-bottom: 26px;
}
.dropdown__item--mb {
  margin-bottom: 42px;
}
.dropdown__link {
  color: var(--text);
}
.dropdown__link--title {
  font-weight: 600;
  color: var(--cod-gray);
}

.receiving {
  display: flex;
  color: var(--white);
}
.receiving__main::before {
  display: inline-block;
  content: "";
  background-color: rgba(255, 169, 0, 0.7);
  width: 65px;
  height: 48px;
  background: url(../images/icons/geolocation.svg) center center/24px 24px no-repeat rgba(255, 169, 0, 0.7);
  border-radius: 16px 0 0 16px;
}
.receiving__secondary {
  width: 230px;
  height: 48px;
  padding-top: 6px;
  background: var(--yellow);
  border-radius: 0 16px 16px 0;
}
.receiving__text {
  display: block;
  font-size: 14px;
}
.receiving__title {
  display: block;
  font-size: 16px;
}

.logo {
  margin-right: 23px;
}

.btn {
  padding: 12px;
  font-size: 18px;
  text-align: center;
  color: var(--white);
  border-radius: 16px;
  background-color: #FF0000;
}

.btn-main {
  padding: 14px 19px 12px 48px;
  width: 132px;
  height: 48px;
  font-size: 16px;
  color: var(--white);
  background-color: var(--red);
  border-radius: var(--border-radius);
}
.btn-main__line {
  position: absolute;
  left: 22px;
  width: 18px;
  height: 2px;
  border-radius: var(--border-radius);
  background: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-main__line:nth-child(1) {
  top: 18px;
}
.btn-main__line:nth-child(2) {
  top: 23px;
}
.btn-main__line:nth-child(3) {
  top: 23px;
}
.btn-main__line:nth-child(4) {
  top: 28px;
}
.btn-main-open .btn-main__line:nth-child(1) {
  transform: scaleX(0);
}
.btn-main-open .btn-main__line:nth-child(2) {
  transform: rotate(45deg);
}
.btn-main-open .btn-main__line:nth-child(3) {
  transform: rotate(-45deg);
}
.btn-main-open .btn-main__line:nth-child(4) {
  transform: scaleX(0);
}

.tabs-buttons__list {
  display: flex;
  gap: 15px;
}

.tabs-buttons {
  width: 221px;
  height: 87px;
  padding: 16px 22px;
  font-size: 20px;
  color: var(--cod-gray);
  border-radius: 16px;
  background-color: var(--serenade);
  border: 1px solid rgba(255, 169, 0, 0.45);
  text-align: left;
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.tabs-buttons__text {
  display: block;
  margin-top: 6px;
  font-size: 16px;
}

.mixitup-control-active {
  border: 1px solid var(--yellow);
  background: var(--yellow);
  color: var(--white);
}

.search {
  position: relative;
  width: 317px;
}
.search__input {
  width: 100%;
  padding: 15px 12px;
  font-size: 16px;
  color: var(--textlight);
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  background-color: var(--white);
}
.search__btn {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 50%;
  transform: translateY(-50%);
  right: 17px;
}

.modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.modal-close {
  position: absolute;
  top: 10px;
  right: 10px;
}

.modal__container {
  position: relative;
  width: 1260px;
  margin: 50px auto;
  display: none;
  vertical-align: middle;
  background-color: #fff;
  border-radius: 20px;
  cursor: default;
  text-align: left;
  height: 805px;
}

@media (max-width: 1024px) {
  .modal__container {
    width: 90%;
  }
}
.modal-content__left {
  width: 39%;
  padding: 33px;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__container.modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.content {
  min-height: 200vh;
  height: 805px;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.favorite {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  width: 50px;
  height: 48px;
  color: #e1e1e1;
  background-color: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .favorite:hover {
    border: 1px solid var(--yellow);
    background-color: #fff8eb;
    color: #ffa900;
  }
}

.user {
  position: relative;
}
.user__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #e1e1e1;
  border-radius: 16px;
  width: 50px;
  height: 48px;
  color: #e1e1e1;
  background-color: var(--white);
  transition-duration: 350ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
@media (any-hover: hover) {
  .user__btn:hover {
    border: 1px solid var(--yellow);
    background-color: #fff8eb;
    color: #ffa900;
  }
}
.user__go-login {
  position: absolute;
  top: 84px;
  left: 50%;
  transform: translate(-50%, 0);
}

.go-login {
  width: 310px;
  padding: 17px 23px 23px;
  border: 1px solid #7E7C7C;
  border-radius: 12px;
  background: var(--white);
}
.go-login__title {
