.tooltip {
  position: relative;
  display: inline-block;
}

.tooltiptext {
  position: absolute;
  padding: 4px;
  font-size: 16px;
  color: var(--textlight);
  text-align: start;
  background-color: #f0f0f0;
  border: 1px solid #c4c4c4;
  border-radius: 8px;
  top: calc(100% + 4px);
  left: 50%;
  transform: translate(-50%, 0);
  box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.05);
  pointer-events: none;
  opacity: 0;
  transition: 1s;
}

.tooltip:hover .tooltiptext {
  opacity: 1;
  z-index: 12;
}